<template>
  <div>
    <el-divider content-position="left"></el-divider>
    <el-table
        :data="coupons"
        border
        style="width: 100%; margin-top: 10px;">
        <el-table-column
          prop="code"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          prop="desc"
          label=""
          width="100">
        </el-table-column>

        <el-table-column
          prop="value"
          label=""
          width="200">
          <template slot-scope="scope">
            {{scope.row.value / 100}}
          </template>
        </el-table-column>

        <el-table-column
          prop="man"
          label=""
          width="200">
          <template slot-scope="scope">
            {{scope.row.man / 100}}
          </template>
        </el-table-column>

        <el-table-column
          prop="vpday"
          label=""
          width="200">
          <template slot-scope="scope">
            {{scope.row.vpday}}
          </template>
        </el-table-column>

        <el-table-column
          fixed="right"
          label=""
          width="300">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row,scope.$index)" type="text" size="small"></el-button>
          </template>
        </el-table-column>

      </el-table>
      <el-button style="width: 100%;margin-top: 5px;" icon="el-icon-plus" @click="addCoupon"></el-button>

      <el-divider content-position="left"></el-divider>
      <el-table
          :data="rules"
          border
          style="width: 100%; margin-top: 10px;">
          <el-table-column
            prop="type"
            label=""
            width="180">
            <template slot-scope="scope">
              <span v-if="scope.row.type == 'register'"></span>
              <span v-if="scope.row.type == 'consumption'"></span>
            </template>
          </el-table-column>
          <el-table-column
            prop="threshold"
            label=""
            width="100">
            <template slot-scope="scope">
              {{scope.row.threshold / 100}}
            </template>
          </el-table-column>

          <el-table-column
            prop="code"
            label=""
            width="200">
          </el-table-column>

          <el-table-column
          fixed="right"
          label=""
          width="300">
          <template slot-scope="scope">
            <el-button @click="handleClick1(scope.row,scope.$index)" type="text" size="small"></el-button>
          </template>
        </el-table-column>
        </el-table>
        <el-button style="width: 100%;margin-top: 5px;" icon="el-icon-plus" @click="addRule"></el-button>


        <el-dialog title="" :visible.sync="dialogFormVisible">

            <el-row>
              <el-col :span="3">
                <div class="title"></div>
              </el-col>
              <el-col :span="6">
                <el-input placeholder="" v-model="coupon.code" clearable></el-input>
              </el-col>
              <el-col :span="3">
                <div class="title"></div>
              </el-col>
              <el-col :span="6">
                <el-input placeholder="" v-model="coupon.desc" clearable></el-input>
              </el-col>
            </el-row>

            <el-row>
              <el-col :span="3">
                <div class="title"></div>
              </el-col>
              <el-col :span="6">
                <el-input placeholder="" v-model="coupon.value" clearable></el-input>
              </el-col>
              <el-col :span="3">
                <div class="title"></div>
              </el-col>
              <el-col :span="6">
                <el-input placeholder="" v-model="coupon.man" clearable></el-input>
              </el-col>
            </el-row>

            <el-row>
              <el-col :span="6">
                <div class="title">()</div>
              </el-col>
              <el-col :span="6">
                <el-input placeholder="()" v-model="coupon.vpday" clearable></el-input>
              </el-col>
            </el-row>


          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="doAddCoupon()"> </el-button>
          </div>
        </el-dialog>

        <el-dialog title="" :visible.sync="dialogFormVisible1">

            <el-row>
              <el-col :span="3">
                <div class="title"></div>
              </el-col>
              <el-col :span="6">
                <el-select v-model="rule.type" filterable placeholder="">
                    <el-option
                      v-for="item in typeList"
                      :key="item.id"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
              </el-col>
            </el-row>

            <el-row>
              <el-col :span="3">
                <div class="title"></div>
              </el-col>
              <el-col :span="6">
                <el-input placeholder="" v-model="rule.threshold" clearable></el-input>
              </el-col>
              <el-col :span="3">
                <div class="title"></div>
              </el-col>
              <el-col :span="6">
                <el-input placeholder="" v-model="rule.code" clearable></el-input>
              </el-col>
            </el-row>

          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible1 = false"> </el-button>
            <el-button type="primary" @click="doAddRule()"> </el-button>
          </div>
        </el-dialog>
  </div>
</template>

<script>
  import axios from 'axios'
  import moment from 'moment'
  import { Loading } from 'element-ui';
  export default {
    data() {
      return {
        coupon:{},
        rule: {},
        q: '',
        dialogFormVisible: false,
        dialogFormVisible1: false,
        coupons: [],
        rules: [],
        typeList: [
          {
            value: "register",
            label: ''
          },
          {
            value: "consumption",
            label: ''
          }
        ],
      }
    },
    methods: {
      dateFormater: function(row, column) {
        var date = row[column.property];
        if (date == 0) {
          return ''
        }
        return moment(date).format("YYYY-MM-DD")
      },
      handleClick(row,index){
        let loadingInstance = Loading.service({ fullscreen: true });
        axios.post('http://localhost:8090/proxy',{id: row._id,tp:'coupondicdelete'}).then(resp => {
          loadingInstance.close()
          this.coupons.splice(index,1)
        })
      },
      handleClick1(row,index){
        let loadingInstance = Loading.service({ fullscreen: true });
        axios.post('http://localhost:8090/proxy',{id: row._id, tp:'couponruledelete'}).then(resp => {
          loadingInstance.close()
          this.rules.splice(index,1)
        })
      },
      addRule() {
        this.dialogFormVisible1 = true
      },
      doAddRule() {
        if (!this.rule.type) {
          this.$message.error("")
          return
        }
        if (!this.rule.threshold) {
          this.$message.error("")
          return
        }
        if (!this.rule.code) {
          this.$message.error("")
          return
        }
        let loadingInstance = Loading.service({ fullscreen: true });
        this.rule.tp = 'couponruleadd'
        axios.post('http://localhost:8090/proxy',this.rule).then(resp => {
          console.log(resp.data)
          this.dialogFormVisible1 = false
          axios.post('http://localhost:8090/proxy',{tp: 'couponrules'}).then(resp => {
            this.rules = resp.data.data
          })
          loadingInstance.close()
        })
      },
      addCoupon() {
         this.dialogFormVisible = true
      },
      doAddCoupon() {
        if (!this.coupon.code) {
          this.$message.error("")
          return
        }
        if (!this.coupon.desc) {
          this.$message.error("")
          return
        }
        if (!this.coupon.value) {
          this.$message.error("")
          return
        }
        if (!this.coupon.man) {
          this.$message.error("")
          return
        }
        if (!this.coupon.vpday) {
          this.$message.error("")
          return
        }
        let loadingInstance = Loading.service({ fullscreen: true });
        this.coupon.tp = 'coupondicadd'
        axios.post('http://localhost:8090/proxy',this.coupon).then(resp => {
          axios.post('http://localhost:8090/proxy',{tp: 'coupondic'}).then(resp => {
            console.log(resp.data)
            this.coupons = resp.data.data
          })
          loadingInstance.close()
          this.dialogFormVisible = false

        })
      }
    },
    mounted() {
      let loadingInstance = Loading.service({ fullscreen: true });
      axios.post('http://localhost:8090/proxy',{tp: 'coupondic'}).then(resp => {
        console.log(resp.data)
        this.coupons = resp.data.data
      })
      axios.post('http://localhost:8090/proxy',{tp: 'couponrules'}).then(resp => {
        console.log(resp.data)
        this.rules = resp.data.data
        loadingInstance.close()
      })

    }
  }
</script>

<style>
</style>



